/* H Space after */
.h_space {
  margin-right: 1.5rem !important;
  margin-top: 1rem !important;
}

/* V Space after */
.mt-1 {
  margin-top: 20rem !important;
  margin-left: 3rem;
}
/* V Space before */
.mt-2 {
  margin-bottom: 2rem !important;
  margin-left: 3rem;
  margin-right: 15rem;
}

.centered {
  height: 100vh; /* Magic here */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

p {
  text-align: left;
}

.p_ind {
  text-indent: 20px;
  text-align: justify;
}

h1 {
  text-align: left;
}

h2 {
  text-align: left;
}

h3 {
  text-align: left;
  font-weight: bold;
}

h5 {
  text-align: left;
  font-weight: bold;
}

